import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AboutComponent } from './pages/about/about.component';
import { EtudesComponent } from './pages/etudes/etudes.component';
import { TravauxComponent } from './pages/travaux/travaux.component';
import { HomeComponent } from './pages/home/home.component';
import { JoinUsComponent } from './pages/join-us/join-us.component';
import { AreaComponent } from './pages/area/area.component';

const routes: Routes = [
  {
    path: '',
    component: HomeComponent,
    data: {
      pageTitle: 'Accueil'
    }
  },
  {
    path: 'nos-metiers',
    component: AreaComponent,
    data: {
      pageTitle: 'Nos métiers'
    }
  },
  {
    path: 'professionnels',
    component: EtudesComponent,
    data: {
      pageTitle: 'Pour les professionnels'
    }
  },
  {
    path: 'nos-offres',
    component: TravauxComponent,
    data: {
      pageTitle: 'Pôle travaux'
    }
  },
  {
    path: 'notre-equipe',
    component: JoinUsComponent,
    data: {
      pageTitle: 'Notre équipe'
    }
  },
  {
    path: '**',
    component: HomeComponent,
    data: {
      pageTitle: 'Page indisponible'
    }
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {
    scrollPositionRestoration: 'enabled',
    onSameUrlNavigation: "reload",
    anchorScrolling: 'enabled',
    scrollOffset: [0, 64], // [x, y] - adjust scroll offset
    initialNavigation: 'enabled'
})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
